<template>
  <Container bordered :active="active" class="crypto-block">
    <Icon
      :name="coin?.icon + '-circle'"
      width="48px"
      height="48px"
    />
    <div>
      <Text variant="p" size="18px" weight="500" mb="9px">{{ coin?.title }}</Text>
      <Text variant="p" size="16px" uppercase>{{ coin?.rate }}</Text>
    </div>
  </Container>
</template>

<script>
import Container from '@/components/Container'
import Icon from '@/components/Icon/Icon'
import Text from '@/components/Text/Text'
import coinsMock from '@/store/mock/coinsMock'
import { computed } from 'vue'

export default {
  name: 'CryptoBlock',
  components: { Icon, Container, Text },
  props: {
    crypto: {
      type: String,
      required: true
    },
    active: {
      type: Boolean,
      default: false
    }
  },
  setup (props) {
    const coin = computed(() => coinsMock
      .find(el => el.key === props.crypto))

    return {
      coin
    }
  }
}
</script>

<style lang="scss">
.crypto-block {
  display: flex;
  align-items: center;
  padding: 20px;
  max-height: 88px;
  cursor: pointer;

  .icon {
    margin-right: 20px;
  }
}
</style>
