<template>
  <Container
    bordered
    box-shadow
    variant="label"
    class="input"
    :class="{
      input__error: error,
      'input__read-only': readOnly,
      'input__hide-shadow': hideShadow || readOnly
    }"
    :style="{
      '--opacity': fullOpacity
    }"
  >
    <div v-if="$slots.prepend" class="input__prepend">
      <slot name="prepend" />
    </div>
    <div class="input__body">
      <span class="input__body-label">{{ label }}</span>
      <div class="input__body__field">
        <span
          v-if="showPlaceholder"
          class="input__body__field-placeholder"
          :style="placeholderStyles"
        >{{ placeholder }}</span>
        <input
          class="input__body__field-input"
          :class="{'input__body-field__opacity': opacity}"
          :readonly="readOnly"
          :disabled="disabled"
          :type="type"
          v-model="inputValue"
          @blur="$emit('blur')"
        />
      </div>
    </div>
    <div v-if="$slots.append" class="input__append">
      <slot name="append" />
    </div>
    <div
      v-if="errorText && error"
      class="input__error-text"
    >
      {{ errorText }}
    </div>
  </Container>
</template>

<script>
import { computed } from 'vue'
import Container from '@/components/Container'

export default {
  name: 'Input',
  components: { Container },
  emits: ['update:modelValue', 'blur'],
  inheritAttrs: true,
  props: {
    type: {
      type: String,
      default: 'text'
    },
    label: {
      type: String,
      default: ''
    },
    modelValue: {
      type: [String, Number],
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    placeholderStyles: {
      type: Object,
      default: () => ({})
    },
    errorText: {
      type: String,
      default: ''
    },
    error: {
      type: Boolean,
      default: false
    },
    readOnly: {
      type: Boolean,
      default: false
    },
    opacity: {
      type: Boolean,
      default: false
    },
    fullOpacity: {
      type: [Number, String],
      default: 1
    },
    hideShadow: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  setup (props, { emit }) {
    const inputValue = computed({
      get: () => props.modelValue,
      set (val) {
        emit('update:modelValue', val)
      }
    })
    const showPlaceholder = computed(() => !inputValue.value)

    return {
      showPlaceholder,
      inputValue
    }
  }
}
</script>

<style lang="scss">
.input {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 14px 9px 20px;

  &__body {
    position: relative;
    width: 100%;

    &-label {
      display: block;

      font-size: 14px;
      line-height: 16px;

      color: #000000;
      opacity: 0.4;
    }
    &__field {
      position: relative;
      width: 100%;
      margin-left: -4px;
      padding-top: 4px;

      &-input {
        width: 100%;
        border: none;
        outline: none;

        font-size: 14px;
        line-height: 16px;

        color: #000000;
        background: transparent;

        &::-webkit-inner-spin-button {
          -webkit-appearance: none;
        }
        &[type=number] {
          -moz-appearance: textfield;
        }
      }
      &-placeholder {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        opacity: 0.4;
      }
    }
  }
  &__prepend {
    margin-right: 10px;
  }
  &__error {
    border: 1px solid map-get($colors, "error");
    box-shadow: $boxShadowError;

    &-text {
      position: absolute;
      top: calc(100% + 6px);
      left: 0;

      font-size: 12px;
      line-height: 14px;
      color: map-get($colors, "error");
    }
  }
  & > div {
    opacity: var(--opacity);
  }
}
.input__read-only.input {
  background-color: #FAFAFA;

  &__body {
    &-field {
      &__opacity {
        opacity: 0.4;
      }
      &:before {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        background: linear-gradient(transparent 150px, white);
      }
    }
  }
}
.input__hide-shadow.input {
  box-shadow: none;
}
</style>
